/** Media Queries **/
.dashboard {
  background-color: #f3f4f6;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  text-align: center;
  font-size: 3.8rem;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashboard .formulario .crear-proyecto {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .dashboard .formulario .crear-proyecto {
    width: auto;
  }
}
.dashboard .formulario .crear-proyecto:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.dashboard .formulario .guardar-perfil {
  background-color: rgb(0, 120, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .dashboard .formulario .guardar-perfil {
    width: auto;
  }
}
.dashboard .formulario .guardar-perfil:hover {
  background-color: #004500;
  cursor: pointer;
}